.contact__inner {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
}
.contact__inner * {
    transition: 1s ease;
    opacity: 0;
}
.contact__bg {
    position: fixed;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(20px) brightness(0.9);
    left: 0;
    top: 0;
}
.contact__title {
    font-size: 45px;
}
.contact__form {
    display: flex;
    align-items: center;
    flex-direction: column;
    row-gap: 40px;
    width: 70%;
    background: rgba(10,10,10, 0.5);
    border-radius: 15px;
    margin-top: 60px;
    padding: 50px 45px;
}
.contact__form-input {
    width: 100%;
    background: none;
    border: 0;
    border-bottom: 2px solid rgba(255,255,255, 0.4);
    color: rgba(255,255,255, 0.4);
    letter-spacing: 0.3px;
    font-family: "Poppins", sans-serif;
    font-size: 16px;
    padding: 12px 10px;
}
.contact__form-message {
    height: 250px;
}
.contact__form-input:focus {
    border-bottom-color: #FFF;
    color: #FFF;
}
.contact__form-btn {
    width: 100%;
    background: none;
    border: 2px solid rgba(255,255,255, 0.4);
    font-size: 16px;
    transition: background .5s ease, color .5s ease;
    cursor: pointer;
    padding: 16px 0;
}
.contact__form-btn:hover {
    background: #FFF;
    color: #000;
}