// Styles
.preload {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100%;
    background: #000;
    transition: 1.1s ease;
    top: 0;
}
.main {
    position: relative;
    height: 100vh;
}
.main__inner {
    display: flex;
    align-items: center;
    height: 100%;
}
.main__inner * {
    transition: 1s ease;
    opacity: 0;
}
.main__bg {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(.5) contrast(1.05);
    left: 0;
    top: 0;
}
.socials {
    display: flex;
    gap: 10px;
}
.socials-link {
    font-size: 20px;
    padding: 20px 10px;
}
.main__socials {
    flex-direction: column;
    margin-left: -10px;
}