@import "_vars.scss";

@media screen and (max-width: 1280px) {

}

@media screen and (max-width: 1024px) {

}

@media screen and (max-width: 767px) {
    .header__inner,
    .menu__header {
        padding: $headerVerticalPadding + px + " " + $sectionHorizontalPaddingMobile + px;
    }
    main {
        padding: $sectionVerticalPadding + px + " " + $sectionHorizontalPaddingMobile + px;
    }
    .about__img {
        width: 100%;
    }
    .songs__grid {
        grid-template-columns: repeat(2, 1fr);
    }
    .contact__form {
        width: 100%;
    }
}

@media screen and (max-width: 480px) {
    .songs__grid {
        grid-template-columns: repeat(1, 1fr);
    }
    .songs__box-play-btn,
    .songs__box-download-btn {
        height: 26px;
    }
}