@import "../../Styles/vars";

.header {
    position: fixed;
    z-index: 100;
    width: 100%;
    top: 0;
}
.header__inner {
    display: flex;
    justify-content: space-between;
    padding: $headerVerticalPadding + px + " " + $sectionHorizontalPadding + px;
}
.header__inner * {
    transition: 1s ease;
    opacity: 0;
}
.logo {
    font-weight: 600;
    font-size: 19px;
    height: fit-content;
}
.header__menu-btn {
    position: relative;
    display: flex;
    row-gap: 6px;
    flex-direction: column;
    width: 32px;
    cursor: pointer;
}
.header__menu-btn,
.menu__header-close-btn {
    box-sizing: content-box;
    margin-right: -25px;
    margin-top: -25px;
    padding: 25px;
}
.header__menu-btn:hover *,
.menu__header-close-btn:hover * {
    background: #FFF;
}
.header__menu-btn * {
    width: 100%;
    height: 2px;
    background: #d3d3e7;
    transition: .3s ease;
}
.header__menu-btn_active .header__menu-line:nth-child(2) {
    display: none;
}
.header__menu-btn_active .header__menu-line:first-child {
    transform: rotateZ(45deg);
    margin-top: 7px;
}
.header__menu-btn_active .header__menu-line:last-child {
    transform: rotateZ(-45deg);
    margin-top: -8px;
}
.menu {
    position: fixed;
    z-index: 200;
    width: 100%;
    height: 0;
    background: #000;
    transition: 1.1s ease;
    bottom: 0;
}
.menu_active {
    height: 100%;
}
.menu__inner {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    overflow: hidden;
}
.menu__inner * {
    transition: 1s ease;
    transition-delay: 0s;
    opacity: 0;
}
.menu__inner_active * {
    transition-delay: 1s;
    opacity: 1;
}
.menu__header {
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 50px 55px;
    top: 0;
}
.menu__header-close-btn {
    position: relative;
    display: flex;
    row-gap: 6px;
    flex-direction: column;
    width: 32px;
    cursor: pointer;
}
.menu__header-close-btn * {
    width: 100%;
    height: 2px;
    background: #d3d3e7;
    transition: .3s ease;
}
.menu__header-btn-line:first-child {
    transform: rotateZ(45deg);
    margin-top: 7px;
}
.menu__header-btn-line:last-child {
    transform: rotateZ(-45deg);
    margin-top: -8px;
}
.menu__nav {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
}
.menu__nav-link {
    font-size: 32px;
    transition: .3s ease;
    padding: 25px 35px;
}
.menu__nav-link:hover {
    font-size: 46px;
}
.menu__socials {
    position: absolute;
    margin-right: -10px;
    right: 55px;
    bottom: 50px;
}
.menu__socials-link {
    margin-bottom: -20px;
}