.songs__box {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    background: rgba(255,255,255, 0.15);
    border-radius: 10px;
    padding: 22px 24px;
}
.songs__box * {
    color: #FFF;
}
.songs__box-cover {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 10px;
}
.songs__box-title,
.songs__box-author,
.songs__box-downloads {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
}
.songs__box-title {
    font-size: 16px;
    margin-top: 15px;
}
.songs__box-author {
    font-size: 13px;
    margin-top: 3px;
    margin-bottom: 15px;
}
.songs__box-author,
.songs__box-downloads {
    color: #d3d3e7;
}
.songs__box-controls {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: auto;
    margin-bottom: 0;
}
.songs__box-play-btn {
    margin-left: 0;
    margin-right: auto;
}
.songs__box-play-btn,
.songs__box-download-btn {
    height: 18px;
    cursor: pointer;
}
.songs__box-downloads {
    font-size: 13px;
    margin-right: 11px;
}