@import "_fonts.scss";
@import "_media.scss";
@import "_vars.scss";

// Reset
* {
    font-family: "Josefin Sans", sans-serif;
    color: #d3d3e7;
    z-index: 1;
    margin: 0;
    padding: 0;
}
html {
    box-sizing: border-box;
}
body {
    background: #050505;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}
a {
    text-decoration: none;
}
button, input, textarea {
    outline: none;
}
textarea {
    resize: none;
}
ul, li {
    list-style: none;
}

// Settings
main {
    padding: $sectionVerticalPadding + px + " " + $sectionHorizontalPadding + px;
    padding-bottom: 60px;
}

// Container
.container {
    width: 100%;
    max-width: $containerWidth + px;
    height: inherit;
    margin: 0 auto;
    padding: 0 $containerPaddings;
}

// Presets
.title_large {
    font-family: "Monument", Arial, sans-serif;
    font-weight: 500;
}
.hover {
    transition: .3s ease;
}
.hover:hover {
    color: #FFF;
}