.about__inner {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.about__inner * {
    transition: 1s ease;
    opacity: 0;
}
.about__title {
    font-size: 45px;
}
.about__img {
    width: 75%;
    border-radius: 10px;
    margin: 65px 0;
}
.about__heading,
.about__desc {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
}
.about__heading {
    font-size: 28px;
}
.about__desc {
    font-size: 17px;
    line-height: 1.8em;
    margin-top: 60px;
}
.about__sign {
    font-family: "The Excited", sans-serif;
    font-size: 46px;
    margin-top: 60px;
}