.songs__inner {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.songs__inner * {
    transition: 1s ease;
    opacity: 0;
}
.songs__bg {
    position: fixed;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(20px) brightness(0.9);
    left: 0;
    top: 0;
}
.songs__title {
    font-size: 45px;
}
.songs__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin-top: 65px;
}